<template>
	<div class="home-box">
		<h1 class="home-title">
			中国经济数据可视化
			<el-popover placement="bottom" trigger="hover">
				<img src="../assets/erweima.png" width="200" />
				<i style="font-size: 16px;" slot="reference" class="el-icon-info"></i>
			</el-popover>
		</h1>
		<el-carousel type="card" class="carousel" height="250px">
			<el-carousel-item class="carousel-card"><img src="../assets/carousel/img1.png" class="carousel-img" />
			</el-carousel-item>
			<el-carousel-item class="carousel-card"><img src="../assets/carousel/img2.png" class="carousel-img" />
			</el-carousel-item>
			<el-carousel-item class="carousel-card"><img src="../assets/carousel/img3.png" class="carousel-img" />
			</el-carousel-item>
			<el-carousel-item class="carousel-card"><img src="../assets/carousel/img4.png" class="carousel-img" />
			</el-carousel-item>
		</el-carousel>
		<div class="home-contant">
			<p>2020年，我国GDP突破100万亿元大关，占世界比重17.38%，人均GDP突破1万美元，形成世界最大规模中等收入群体。</p>
			<div ref="chart1" class="home-chart"></div>
			<p>过去10年里，我国经济持续发展，在攻坚克难中书写了新的历史巨变：农业转移人口成为新市民，贫困人口实现脱贫，困扰中华民族几千年的绝对贫困问题将历史性得到解决；经济实力、科技实力、综合国力跃上新的大台阶。</p>
			<div ref="chart2" class="home-chart"></div>
			<p>本项目收集了2011-2020年内各省份的人口、GDP、工资以及房价数据进行可视化。（数据来源： <a href='https://www.baidu.com/' target="_blank">万能的互联网</a>）</p>
			<p class="home-contant-foot"><sub class="el-icon-date"> 2021/12/30</sub></p>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Home",
		data() {
			return {
				dataList1: null,
				dataList2: null
			}
		},
		methods: {
			drawChart1() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.chart1);
				if (!chart) {
					chart = this.$echarts.init(this.$refs.chart1);
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					color: ['rgba(6,219,92,0.5)', 'rgba(255,0,0,0.5)', 'rgba(255,192,203,0.5)', 'rgba(55,162,218,0.5)',
						'rgba(255,219,92,0.5)', 'rgba(0,0,92,0.5)'
					],
					title: {
						text: '2020世界GPD前5',
						left: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "<b>{a}</b> <br/>{b} : {c} <i>({d}%)</i>"
					},
					legend: {
						top: 'bottom',
						left: 'center'
					},
					series: [{
						name: 'GDP(万亿美元)',
						type: 'pie',
						radius: ['35%', '75%'],
						itemStyle: {
							borderRadius: 10
						},
						label: {
							show: false,
							position: 'center'
						},
						labelLine: {
							show: false
						},
						avoidLabelOverlap: false,
						emphasis: {
							label: {
								show: true,
								fontSize: '26',
								fontWeight: 'bold'
							}
						},
						data: this.dataList1
					}]
				}
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			},
			drawChart2() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.chart2);
				if (!chart) {
					chart = this.$echarts.init(this.$refs.chart2);
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					title: {
						text: '中国GDP',
						left: 'center'
					},
					tooltip: {
						trigger: 'item'
					},
					xAxis: {
						type: 'category',
						data: [2011, 2012, 2013, 2014, 2015, 2016, 2017, 2018, 2019, 2020]
					},
					yAxis: {
						type: 'value'
					},
					series: [{
						type: 'line',
						name: 'GDP(万亿美元)',
						data: this.dataList2[0],
						itemStyle: {
							color: '#F57474'
						}
					}, {
						type: 'bar',
						name: '占世界(%)',
						data: this.dataList2[1],
						itemStyle: {
							color: 'rgba(131,191,246,0.5)'
						}
					}]
				}
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'data/world-gdp.dat').then(resp => {
				this.dataList1 = eval(resp.data);
				this.drawChart1();
			})
			this.$axios.get(process.env.BASE_URL + 'data/china-gdp.dat').then(resp => {
				this.dataList2 = resp.data;
				this.drawChart2();
			})
		}
	}
</script>

<style>
	.home-title {
		text-align: center;
		font-size: 25px;
	}

	.home-contant>p {
		text-indent: 2em;
	}

	.home-contant a {
		color: #409eff;
		text-decoration: none;
	}
	
	.home-contant a:hover {
		text-decoration: none;
	}

	.home-contant-foot {
		margin: 2px 10px;
		text-align: right;
	}

	.home-chart {
		margin: auto;
		min-width: 300px;
		width: 50%;
		height: 300px;
	}

	.carousel {
		width: 80%;
		margin: auto;
		min-width: 300px;
	}

	.carousel-card {
		border-radius: 5px;
	}

	.carousel-img {
		width: 100%;
		height: 100%;
	}
</style>
